import React from 'react';
import { Link, useNavigate } from "react-router-dom";
import { Button, Form, Col } from 'react-bootstrap';
import { RegisterAccount } from '../../services/account-service';
import { NotificationManager } from 'react-notifications';

class Register extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            email: "",
            password: "",
            confirmPassword: "",
            validated:false,
        }
    }

    EmailChange = (email) => {
        this.setState({ email: email.target.value });
    }

    PasswordChange = (password) => {
        this.setState({ password: password.target.value });
    }

    ConfirmPasswordChange = (confirmPassword) => {
        this.setState({ confirmPassword: confirmPassword.target.value });
    }

    EmailInvalid = () => {
        const emailRule = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/;
        return this.state.validated && !emailRule.test(this.state.email);
    }

    PasswordInvalid = () => {
        return this.state.validated && this.state.password.length<6;
    }

    ConfirmPasswordInvalid = () => {
        return this.state.validated && (this.state.confirmPassword==="" || this.state.password !== this.state.confirmPassword);
    }

    RegisterClick = async (event) => {
        event.preventDefault();
        event.stopPropagation();

        await this.setState({validated:true })

        const invalids=[
            this.EmailInvalid(),
            this.PasswordInvalid(),
            this.ConfirmPasswordInvalid(),
        ]

        if (invalids.some(invaild=>invaild)) return;
        
        RegisterAccount(
            this.state.email,
            this.state.password,
            () => {
                NotificationManager.success('成功', '註冊', 3000);
                this.props.navigate('/login');
            },
            (error) => {
                if (error.toString().indexOf("auth/email-already-in-use") !== -1) {
                    NotificationManager.error('電子郵件已註冊', '註冊', 3000);
                }

                console.log("error", error);
            }
        )
    }

    render() {
        return (
            <div className="mx-auto" style={{ maxWidth: '330px', width: '100%', padding: '50px 15px 15px 15px' }}>
                <p className="fs-2 text-center">註冊帳號</p>
                <Form noValidate onSubmit={this.RegisterClick}>
                    <div className="mb-2">
                        <Form.Group className="position-relative" as={Col} md="12" controlId="email">
                            <Form.Control
                                required
                                type="email"
                                placeholder="請輸入電子郵件"
                                value={this.state.email}
                                onChange={this.EmailChange}
                                isInvalid={this.EmailInvalid()}
                            />
                            <Form.Control.Feedback className="text-nowrap" style={{left:"102%",top:"50%",transform:"translateY(-50%)"}} type="invalid" tooltip>{this.state.email===""?"必填":"請輸入Email格式"}</Form.Control.Feedback>
                        </Form.Group>
                    </div>
                    <div className="mb-2">
                        <Form.Group className="position-relative" as={Col} md="12" controlId="password">
                            <Form.Control
                                required
                                type="password"
                                placeholder="請輸入密碼"
                                value={this.state.password}
                                onChange={this.PasswordChange}
                                isInvalid={this.PasswordInvalid()}
                            />
                            <Form.Control.Feedback className="text-nowrap" style={{left:"102%",top:"50%",transform:"translateY(-50%)"}} type="invalid" tooltip>{this.state.password===""?"必填":"密碼長度需大於6"}</Form.Control.Feedback>
                        </Form.Group>
                    </div>
                    <div className="mb-3">
                        <Form.Group className="position-relative" as={Col} md="12" controlId="password">
                            <Form.Control
                                required
                                type="password"
                                placeholder="再次確認您的密碼"
                                value={this.state.confirmPassword}
                                onChange={this.ConfirmPasswordChange}
                                isInvalid={this.ConfirmPasswordInvalid()}
                            />
                            <Form.Control.Feedback className="text-nowrap" style={{left:"102%",top:"50%",transform:"translateY(-50%)"}} type="invalid" tooltip>{this.state.confirmPassword===""?"必填":"兩次輸入的密碼必須一致"}</Form.Control.Feedback>
                        </Form.Group>
                    </div>
                    <div className="d-flex justify-content-around">
                        <Button className="me-1 col-6" type="submit">註冊</Button>
                        <Link className="btn btn-danger col-6" to={'./../login'}>返回</Link>
                    </div>
                </Form>
                
            </div>
        )
    }
}

export default function RegisterNavigate(props) {
    const navigate = useNavigate();

    return <Register {...props} navigate={navigate} />;
}